body, html {
    height: 100%;
    overflow-y: hidden;
    background: linear-gradient(39deg, rgba(255,0,212,1) 0%, rgba(0,46,255,1) 31%, rgba(94,214,239,1) 97%);

}
* {
    font-family: sans-serif;
    
    background-repeat: no-repeat;
    background-size: auto;
    color: #000;
}

.container {
    border: 1px unset red, green;
    border: 3px solid #fff;
    background: rgba(255, 255, 255, 0.6);
    width: auto;
    height: 480px;
    margin: auto;
    margin-top: 20px;
    border-radius: 10px;
    text-align: center;
}

.icon {
    width: 130px;
    height: 130px;
    position: relative;
    bottom: 20px;
    display: block;
    margin: auto;
}

.temperature {
    background: linear-gradient(39deg, rgba(2,0,36,1) 0%, rgba(94,214,239,1) 0%, rgba(0,46,255,1) 31%, rgba(255,0,212,1) 97%);

    -webkit-text-fill-color: 
    transparent;
    -webkit-background-clip: text;
    display: block;
    font-size: 30px;
    margin-top: -35px;
}

.city {
    color: #ffffff;
    background: linear-gradient(39deg, rgba(0,46,255,1) 29%, rgba(255,0,212,1) 71%, rgba(249,8,213,1) 72%, rgba(94,214,239,1) 97%);

    -webkit-text-fill-color: 
    transparent;
    -webkit-background-clip: text;
    display: block;
    font-size: 40px;
    margin: 6px;
    margin-top: 20px;
    

}



.description {
    font-size: 30px;


    background: linear-gradient(39deg, rgba(2,0,36,1) 0%, rgba(94,214,239,1) 0%, rgba(0,46,255,1) 31%, rgba(255,0,212,1) 97%);

    -webkit-text-fill-color: 
    transparent;
    -webkit-background-clip: text;
}

.min {
    background: linear-gradient(39deg, rgba(21,0,196,1) 0%, rgba(30,110,252,1) 87%);

    -webkit-text-fill-color: 
    transparent;
    -webkit-background-clip: text;
}

.max {
    background: linear-gradient(39deg, rgba(255,0,0,1) 0%, rgba(239,159,94,1) 51%);

    -webkit-text-fill-color: 
    transparent;
    -webkit-background-clip: text;
}

.feel-like {
    background: linear-gradient(39deg, rgba(160,0,255,1) 0%, rgba(230,94,239,1) 51%);

    -webkit-text-fill-color: 
    transparent;
    -webkit-background-clip: text;
}

.pressure {
    background: linear-gradient(39deg, rgba(128,0,0,1) 0%, rgba(255,0,0,1) 50%);

    -webkit-text-fill-color: 
    transparent;
    -webkit-background-clip: text;
}

.humidity {
    background: linear-gradient(39deg, rgba(0,21,128,1) 0%, rgba(9,109,171,1) 34%, rgba(27,0,255,1) 100%);

    -webkit-text-fill-color: 
    transparent;
    -webkit-background-clip: text;
}

.wind {
    background: linear-gradient(39deg, rgba(123,123,123,1) 65%, rgba(230,230,230,1) 100%);
    -webkit-text-fill-color: 
    transparent;
    -webkit-background-clip: text;
}



.min, .max, .feel-like, .pressure, .humidity, .wind {
    font-size: 20px;
    text-align: right;
    padding: 10px;
}

.description-container {
    position: relative;
    top: 10px;
}